/* 板块居中 */
.wide {
  width: 1200px;
  margin: 0 auto;
}
/* 清除浮动 */
.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
}
.clearfix:after {
  clear: both;
}
/* 对齐方式 */
.fl {
  float: left;
}
.fr {
  float: right;
}
.tl {
  text-align: left;
}
.tc {
  text-align: center;
}
.tr {
  text-align: right;
}
/*隐藏*/
.none {
  display: none;
}
.hide {
  visibility: hidden;
  opacity: 0;
}
.visible {
  visibility: visible;
  opacity: 1;
}
/* 单行省略 */
.ellipse {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/* 多行省略 */
.ellipselist {
  text-overflow: -o-ellipsis;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
/*行内元素*/
.child-inlineblock > * {
  display: inline-block;
  vertical-align: middle;
  *display: inline;
  *zoom: 1;
}
.inlineblock {
  display: inline-block;
  vertical-align: middle;
  *display: inline;
  *zoom: 1;
}
/*icon 小图标*/
.icon {
  display: inline-block;
  vertical-align: middle;
}
